<template>
  <el-row type="flex" justify="end">
    <el-col :span="24">
      <ul class="h_nav">
        <li>Home</li>
        <li>Works</li>
        <li>RoadMap</li>
        <li>Team</li>
      </ul>
    </el-col>
  </el-row>
</template>

<script>
export default {
  name: "Nav",
  data() {
    return {
      active: 0, // 
      scrollTop: "", //
    };
  },
  mounted() {
    window.addEventListener("scroll", this.onScroll, false);
  },
  destroyed() {
    window.removeEventListener("scroll", this.onScroll);
  },
  methods: {
    handleScroll() {
      const scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop; //
      this.scrollTop = scrollTop; //datareturnscrollTop
      console.log(scrollTop);
      //scrollTop
      if (scrollTop > 1115) {
        this.active = 1;
      }
      if (scrollTop > 1850) {
        this.active = 2;
      }
      if (scrollTop < 1000) {
        this.active = 0;
      }
    },
  },
};
</script>

